@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@400;600;800&display=swap);
body {
  margin: 0;
  font-family: 'Roboto Slab', sans serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.Navbar{background-color:antiquewhite;color:darkgray}.Navbar .Navbar-items{display:flex;justify-content:space-between}.Navbar ul{list-style-type:none;margin:0;padding:0;justify-content:space-between}.Navbar ul li{display:inline}

.Home{color:grey}.Home .Home-title{color:white;background-color:rgba(0,0,0,0.5);padding:10px 30px;border-radius:3px;text-align:center}@media only screen and (max-width: 600px){.Home .Home-title{font-size:2rem;padding:10px 20px}}@media only screen and (max-width: 600px){.Home .Home-category-title{font-size:1.5rem}}.Home .Home-btn-section{display:flex;justify-content:center}.Home .Home-btn-section .Home-gender-btn{padding:3px 8px;background-color:darksalmon;margin:0 10px;border-radius:3px}.Home .hero{width:100%;height:300px;object-fit:cover}.button{height:100%;display:flex;align-items:center;justify-content:center;width:140px;height:45px;font-family:'Roboto-Slab', sans-serif;font-size:12px;text-transform:uppercase;letter-spacing:2.5px;font-weight:600;color:#fff;background-color:#858585;border:none;border-radius:4px;box-shadow:0px 8px 15px rgba(0,0,0,0.1);transition:all 0.3s ease 0s;cursor:pointer;outline:none}.button:hover{background-color:#2ee59d;box-shadow:0px 15px 20px rgba(46,229,157,0.4);color:#fff;transform:translateY(2px);text-decoration:none}.button.men{margin-right:2vw}.button.women{margin-left:2vw}

.Event-btn{color:white;font-weight:600;font-size:35px;background-color:darkslategrey;border-radius:50%;width:165px;height:165px;display:flex;justify-content:center;align-items:center;margin:auto}@media only screen and (max-width: 767px){.Event-btn{width:130px;height:130px;font-size:30px}}.Event-btn:hover{text-decoration:none;color:white}

.Record ul{list-style:none;padding:0;margin:0}.Record img{width:100%;background-color:#b6b6b6}@media only screen and (max-width: 992px){.Record img{margin-top:20px}}@media only screen and (max-width: 500px){.Record img{margin-top:0}}.Record .flag{width:30px}.Record svg{width:30px;height:30px}.Record .Record-name{font-size:28px;font-weight:800;font-style:italic}.Record .Record-time{font-size:36px;font-weight:800;font-style:italic;text-align:center;margin-top:30px}.Record .Record-age{font-style:italic;color:dimgrey;margin-bottom:20px}.Record .Record-details span{font-weight:600}.Record .Record-details li{border:1px solid lightgrey;padding:5px}

.App{font-family:'Roboto Slab', sans serif;color:#444444}

